import React, { useContext } from 'react'
import Topbar from './Topbar'
import { makeStyles, CssBaseline, Container } from '@material-ui/core'

import { AppContext } from '../contexts'
import ResultsPaneConnect from './SearchResults/SearchResults.connect'
import SearchFormConnect from './SearchForm/SearchForm.connect'
import ProgramChooser from './ProgramChooser'
import Footer from './Footer'

const useStyles = makeStyles(theme => ({
  root: {
    height: '100%',
    overflow: 'hidden',
    display: 'flex',
    flexDirection: 'column'
  },
  inner: {
    flexGrow: 1,
    overflowX: 'hidden',
    overflowY: 'scroll',
    display: 'flex',
    flexDirection: 'column'
  },
  spacer: {
    flex: '1 1'
  }
}))

/**
 * This is the over-arching wrapper for the app. It combines all the
 * same place. Time will tell if that's a good strategy.
 */
const App = () => {
  const { program } = useContext(AppContext)
  const classes = useStyles()
  return (

    <div className={classes.root}>
      <CssBaseline />
      <Topbar />
      <div className={classes.inner}>
        {!program ? (
          <Container maxWidth='sm'>
            <ProgramChooser />
          </Container>
        ) : (
          <Container maxWidth='md'>
            <React.Fragment>
              <SearchFormConnect />
              <ResultsPaneConnect />
            </React.Fragment>
          </Container>
        )}
        <div className={classes.spacer} />
        <Footer />
      </div>
    </div>

  )
}

export default App
