import React, { useState, useEffect, useRef } from 'react'
import PropTypes from 'prop-types'
import { ThemeProvider } from '@material-ui/styles'
import { useAuthentication, Loading } from '@riverscapes-client/auth'
import { api } from '@riverscapes/common'
import _ from 'lodash'
import { AppContext } from '../contexts'
import App from './App'
import { endpoints, aws, appDefaults, mandatoryMetaColors } from '../config'
import themes from '../themes'

/**
 * This is the over-arching wrapper for the app. It combines all the
 * same place. Time will tell if that's a good strategy.
 */
const AppConnect = ({ location, history }) => {
  const [program, setProgram] = useState()
  const [programs, setPrograms] = useState()
  const [pendingZips, setPendingZips] = useState({})
  const pendingZipsRef = useRef()
  pendingZipsRef.current = pendingZips

  const [isLoading, setIsLoading] = useState(false)
  const [searchParams, setSearchParams] = useState(appDefaults.searchDefaultParams) // maybe context?
  const { signedIn, authData, signOut, authScreen } = useAuthentication(aws)

  const reset = () => {
    setProgram(null)
    setSearchParams(appDefaults.searchDefaultParams)
  }

  let progMachineName = ''
  try {
    progMachineName = location.pathname.split('/').filter(s => s.length > 0)[0]
  }
  catch {}

  const downloadFile = ({ projType, id }, key) => {
    return api.downloadFile(endpoints.graphql, context.jwt, projType, program.machineName, id, key)
      .then(data => {
        try {
          window.open(data.data.downloadFile.downloadUrl, '_self')
        }
        catch (err) {
          console.log('DOWNLOAD err', err)
        }
      })
      .catch(err => {
        console.log('DOWNLOAD err', err)
      })
  }

  const downloadZipFile = ({ projType, id }) => {
    const checkZipFunc = async () => {
      const data = await api.downloadZipFile(endpoints.graphql, context.jwt, projType, program.machineName, id)
        .catch(err => {
          console.log('DOWNLOAD err', err)
        })
      console.log(data)
      try {
        if (!data.data.downloadZip.pendingSince) {
          setPendingZips(
            _.pickBy(pendingZipsRef.current, (val, key) => key !== id)
          )
          window.open(data.data.downloadZip.downloadUrl, '_self')
        }
        else {
          setPendingZips({
            ...pendingZipsRef.current,
            [id]: data.data.downloadZip.pendingSince
          })
          setTimeout(checkZipFunc, 5000)
        }
      }
      catch (err) {
        console.log('DOWNLOAD err', err)
      }
    }

    // try once
    return checkZipFunc()
  }

  const context = {
    jwt: authData && authData.signInUserSession && authData.signInUserSession.accessToken.jwtToken,
    url: endpoints.graphql,
    program,
    searchParams,
    setSearchParams,
    programs,
    downloadFile,
    downloadZipFile,
    pendingZips,
    navigateProgram: pName => history.push('/' + pName),
    signOut,
    location,
    history
  }

  useEffect(() => {
    reset()
    api.getPrograms(endpoints.graphql).then(allPrograms => {
      console.log('API GetPrograms', allPrograms)
      if (allPrograms.data && allPrograms.data.programs) {
        setPrograms(allPrograms.data.programs)
      }
    })
  }, [])

  useEffect(() => {
    reset()
    const fetchData = async () => {
      if (!progMachineName || progMachineName.length === 0) return
      setIsLoading(true)
      if (programs && programs.length > 0) {
        const program = programs.find(p => p.machineName === progMachineName)
        if (program) {
          program.definitions.Collections = program.definitions.Collections.map((c, idx) => ({
            ...c,
            color: mandatoryMetaColors[idx % mandatoryMetaColors.length]
          }))
          setProgram(program)
        }
      }
      else {
        reset()
      }
      setIsLoading(false)
    }
    fetchData()
  }, [progMachineName, programs, context.url])

  const showSignin = (programs && program && signedIn === null)

  return (
    <ThemeProvider theme={themes.home}>
      {!programs && <Loading message='Loading program list...' />}
      {showSignin && authScreen}
      {programs && signedIn && isLoading && <Loading message='Loading program...' />}
      {!showSignin && !isLoading && (
        <AppContext.Provider value={context}>
          <App />
        </AppContext.Provider>
      )}
    </ThemeProvider>
  )
}
AppConnect.propTypes = {
  location: PropTypes.object,
  history: PropTypes.object
}

export default AppConnect
