import React, { useContext } from 'react'
import PropTypes from 'prop-types'
import Typography from '@material-ui/core/Typography'
import Skeleton from '@material-ui/lab/Skeleton'
import ListItem from '@material-ui/core/ListItem'
import ListItemAvatar from '@material-ui/core/ListItemAvatar'
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction'
import Avatar from '@material-ui/core/Avatar'
import moment from 'moment'
import IconButton from '@material-ui/core/IconButton'
import Button from '@material-ui/core/Button'
import CircularProgress from '@material-ui/core/CircularProgress'
// import FolderIcon from '@material-ui/icons/Folder'
import CloudDownloadIcon from '@material-ui/icons/CloudDownload'

import { AppContext } from '../../contexts'
import { makeStyles } from '@material-ui/core/styles'
import { Chip } from '@material-ui/core'

const useStyles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(2),
    margin: theme.spacing(2)
  },
  itemText: {
    flexGrow: 1
  },
  mm: {
    display: 'flex',
    width: '100%'
  },
  prodChip: {
    height: theme.spacing(2.3),
    borderRadius: theme.spacing(1) / 2,
    color: '#999',
    border: `1px solid #ddd`,
    background: '#eee',
    marginRight: theme.spacing(1)
  },
  downloadText: {
    position: 'absolute',
    textAlign: 'center',
    width: '100%',
    bottom: -theme.spacing(2),
    right: 0
  }
}))

const SearchResult = ({ data, loading, onClick }) => {
  const classes = useStyles()
  const { program, pendingZips, downloadZipFile } = useContext(AppContext)
  const collections = program.definitions.Collections

  if (loading) {
    return (
      <ListItem button>
        <ListItemAvatar>
          <Skeleton variant='circle' height={40} width={40} />
        </ListItemAvatar>
        <div className={classes.itemText}>
          <Skeleton style={{ height: 10, width: '60%', margin: '6px 0' }} />
          <Skeleton style={{ height: 10, width: '40%', margin: '6px 0' }} />
        </div>
        <ListItemSecondaryAction>
          <IconButton edge='end' disabled>
            <Skeleton variant='circle' height={40} width={40} />
          </IconButton>
        </ListItemSecondaryAction>
      </ListItem>
    )
  }

  let mandatoryMeta = []

  let pTypeName = data.projType
  const pendingSince = pendingZips[data.id]
  // Figure out which fields are mandatory
  if (data.projType && program.products[data.projType]) {
    const projObj = program.products[data.projType].pathArr.filter(pr => pr.type === 'Collection')
    pTypeName = program.products[data.projType].name
    mandatoryMeta = projObj.map(po => ({ key: po.def.name, val: data.meta[po.def.name] }), {})

    // If no mandatory meta values are present just show the first 5
    if (mandatoryMeta.length === 0) {
      mandatoryMeta = Object.keys(data.meta)
        .slice(0, 5)
        .map(po => ({ key: po, val: data.meta[po] }), {})
    }
  }
  return (
    <ListItem button onClick={onClick}>
      <ListItemAvatar>
        <Avatar> {data.projType.substr(0, 1)} </Avatar>
      </ListItemAvatar>
      <div className={classes.itemText}>
        <Typography variant='body1'>{data.name}</Typography>
        <div className={classes.mm}>
          <Chip className={classes.prodChip} label={pTypeName} size='small' />
          {mandatoryMeta.map(({ key, val }, idx) => {
            const mmFound = collections.find(c => c.name === key)
            const color = mmFound ? mmFound.color[500] : null
            return (
              <Typography
                key={`mmval-${idx}`}
                className={classes.mmItem}
                variant='caption'
                color='textSecondary'
              >
                {idx > 0 && '  ' }
                <span style={{ color }}> {val} </span>
              </Typography>
            )
          })}
        </div>
      </div>
      <ListItemSecondaryAction>
        {pendingSince
          ? (
            <Button
              variant='outlined'
              edge='end'
              disabled
              color='primary'
              className={classes.button}
              startIcon={<CircularProgress color='secondary' size={24} thickness={3} />}
            >
              Preparing Zip...
            </Button>
          )
          : (
            <Button
              edge='end'
              variant='outlined'
              color='primary'
              onClick={() => downloadZipFile(data)}
              end
              startIcon={<CloudDownloadIcon />}
            >
              ZIP
            </Button>
          )
        }
        {pendingSince && (
          <Typography className={classes.downloadText} component='div' color='textSecondary' variant='caption'>
          started {moment(pendingSince, 'x').fromNow()}
          </Typography>
        )}
      </ListItemSecondaryAction>
    </ListItem>

  )
}

SearchResult.propTypes = {
  data: PropTypes.object,
  loading: PropTypes.bool,
  onClick: PropTypes.func
}

export default SearchResult
