import React, { useContext, useState, useEffect } from 'react'
// import PropTypes from 'prop-types'
import { AppContext } from '../../contexts'
import { api } from '@riverscapes/common'
import SearchResults from './SearchResults'

const ResultsPaneConnect = () => {
  const { jwt, url, program, searchParams } = useContext(AppContext)
  const [isLoading, setIsLoading] = useState(true)
  const [results, setResults] = useState()
  const [errors, setErrors] = useState()

  useEffect(() => {
    setResults(null)
    async function doEffect () {
      if (!program) return
      setIsLoading(true)
      console.log('SEARCH', program.machineName, searchParams)
      const results = await api.search(url, jwt, program.machineName, {
        ...searchParams
      }).catch(err => {
        setIsLoading(false)
        setErrors([err])
      })
      try {
        if (results.errors && results.errors.length > 0) {
          setErrors(results.errors)
          setIsLoading(false)
        }
        else {
          const searchData = results.data.search
          const startIndex = searchParams.nextToken || 0
          const endIndex = searchData.projects.length > 0 ? startIndex + searchData.projects.length - 1 : 0
          const total = searchData.total || 0

          setResults({
            ...searchData,
            startIndex,
            endIndex,
            total
          })
          setIsLoading(false)
        }
      }
      catch {
        setIsLoading(false)
      }
    }
    doEffect()
  }, [program, jwt, url, searchParams])

  return (
    <SearchResults data={results} loading={isLoading} errors={errors} />
  )
}

export default ResultsPaneConnect
