import React, { useContext } from 'react'
// import PropTypes from 'prop-types'
import Paper from '@material-ui/core/Paper'
import Avatar from '@material-ui/core/Avatar'
import Typography from '@material-ui/core/Typography'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'
import ListItemAvatar from '@material-ui/core/ListItemAvatar'
// import MenuIcon from '@material-ui/icons/Menu'
import { makeStyles } from '@material-ui/core/styles'

import { AppContext } from '../contexts'

const useStyles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(2),
    margin: theme.spacing(2)
  }
}))

const ProgramChooser = () => {
  const classes = useStyles()
  const { programs, navigateProgram } = useContext(AppContext)
  return (
    <Paper className={classes.root} elevation={1}>
      <Typography variant='subtitle1'>
      Choose a program:
      </Typography>
      <List>
        {programs && programs.map((p, idx) => (
          <ListItem key={`progKey-${idx}`} button divider onClick={() => navigateProgram(p.machineName)}>
            <ListItemAvatar>
              <Avatar alt='programname'>
                {p.name.substr(0, 1)}
              </Avatar>
            </ListItemAvatar>
            <ListItemText primary={p.name} />
          </ListItem>
        ))}
      </List>
    </Paper>
  )
}

// ProgramChooser.propTypes = {
//   prop1: PropTypes.object.isRequired
// }

export default ProgramChooser
