import React, { useContext } from 'react'
import PropTypes from 'prop-types'
import Button from '@material-ui/core/Button'
import Typography from '@material-ui/core/Typography'

import { AppContext } from '../contexts'
import { makeStyles } from '@material-ui/core/styles'

const useStyles = makeStyles(theme => ({
  root: {

  },
  words: {
    textAlign: 'center'
  },
  nav: {
    display: 'flex'
  },
  spacer: {
    flexGrow: 1
  },
  numButton: {
    minWidth: theme.spacing(4)
  }
}))

const Pagination = ({ data, loading }) => {
  const classes = useStyles()
  const { searchParams, setSearchParams } = useContext(AppContext)

  if (loading) {
    return (
      <div className={classes.root}>
        <div className={classes.nav}>
          <Button disabled color='primary'>Previous</Button>
          <div className={classes.spacer} />
          <div className={classes.spacer} />
          <Button disabled color='primary'>Next</Button>
        </div>
        <div className={classes.words}>
          <Typography variant='caption'>
            Loading...
          </Typography>
        </div>
      </div>
    )
  }
  if (!data) return null
  const { startIndex, endIndex, total } = data

  const nextToken = data.nextToken || 0
  const previousToken = data && data.projects && startIndex - data.projects.length > 0 ? startIndex - data.projects.length : 0
  const currPage = parseInt(Math.ceil((startIndex + 1) / searchParams.limit), 10)

  const totalPages = searchParams.limit > 0 ? parseInt(Math.ceil(total / searchParams.limit), 10) : 0
  let showJumpNums = []
  if (totalPages > 1 && totalPages <= 10) showJumpNums = Array(totalPages).fill(null).map((noVal, idx) => idx + 1)
  else if (totalPages > 10) {
    if (currPage <= 5 || currPage >= totalPages - 5) {
      showJumpNums = [1, 2, 3, 4, 5, null, totalPages - 4, totalPages - 3, totalPages - 2, totalPages - 1, totalPages]
    }
    else {
      showJumpNums = [1, 2, null, currPage - 2, currPage - 1, currPage, currPage + 1, currPage + 2, null, totalPages - 1, totalPages]
    }
  }

  const handleNext = () => setSearchParams({ ...searchParams, nextToken })
  const handlePrevious = () => setSearchParams({ ...searchParams, nextToken: previousToken })
  const handleJump = (jumpNum) => {
    setSearchParams({ ...searchParams, nextToken: searchParams.limit * (jumpNum - 1) })
  }

  return (
    <div className={classes.root}>
      <div className={classes.nav}>
        <Button disabled={startIndex === 0} onClick={handlePrevious} color='primary'>Previous</Button>
        <div className={classes.spacer} />
        {showJumpNums.map(jn => {
          if (jn !== null) {
            if (jn === currPage) {
              return (
                <Button
                  className={classes.numButton}
                  key={`page-${jn}`}
                  disabled
                  color='inherit'
                >
                  {jn}
                </Button>
              )
            }
            else {
              return (
                <Button
                  className={classes.numButton}
                  key={`page-${jn}`}
                  onClick={() => handleJump(jn)}
                  color='primary'
                >
                  {jn}
                </Button>
              )
            }
          }
          else {
            return (
              <Button key={`page-${jn}`} disabled>
          ...
              </Button>
            )
          }
        })}
        <div className={classes.spacer} />
        <Button disabled={endIndex >= totalPages} onClick={handleNext} color='primary'>Next</Button>
      </div>
      <div className={classes.words}>
        <Typography variant='caption'>
        Results <strong>{startIndex + 1}</strong> - <strong>{endIndex + 1}</strong> of <strong>{total}</strong>
        </Typography>
      </div>
    </div>
  )
}

Pagination.propTypes = {
  data: PropTypes.object,
  loading: PropTypes.bool
}

export default Pagination
