import React, { useState, useContext } from 'react'
import PropTypes from 'prop-types'
import TextField from '@material-ui/core/TextField'
import Typography from '@material-ui/core/Typography'
import FormControl from '@material-ui/core/FormControl'
import Select from '@material-ui/core/Select'
import MenuItem from '@material-ui/core/MenuItem'
import InputLabel from '@material-ui/core/InputLabel'
import Button from '@material-ui/core/Button'
import SearchIcon from '@material-ui/icons/Search'
import { util } from '@riverscapes/common'
import { AppContext } from '../../contexts'
import { appDefaults } from '../../config'
import { makeStyles, Paper } from '@material-ui/core'

const useStyles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(2),
    margin: theme.spacing(2)
  },
  spacer: {
    flexGrow: 1
  },
  actionBar: {
    display: 'flex',
    width: '100%',
    '&>*': {
      marginLeft: theme.spacing(1)
    }
  },
  projTypeLine: {
    display: 'flex'
  },
  projType: {
    marginRight: theme.spacing(1),
    flexGrow: 1,
    maxWidth: 300
  },
  resultsNum: {
    flexShrink: 0
  },
  projTypeMenuItem: {
    display: 'flex'
  },
  projTypeMenuItemSelected: {
    display: 'flex'
  },
  projTypeMenuItemName: {
    flexGrow: 1
  },
  projTypeMenuItemNum: {
    color: theme.palette.text.secondary,
    fontSize: '0.75rem'
  }
}))

/**
 * This is the over-arching wrapper for the app. It combines all the
 * same place. Time will tell if that's a good strategy.
 */
const SearchForm = ({ projTotals }) => {
  const classes = useStyles()
  const { program, setSearchParams } = useContext(AppContext)
  const [searchParamsTmp, setSearchParamsTmp] = useState(appDefaults.searchDefaultParams) // maybe context?
  const [advancedVals, setAdvancedVals] = useState([])

  const toggleAdvanced = () => {
    if (advancedVals.length > 0) {
      submitSearch({ ...searchParamsTmp }, [])
    }
    else {
      submitSearch({ ...searchParamsTmp }, [['', '']])
    }
  }

  const projectDict = program && program.products && Object.values(program.products).map(({ name, id }) => ({ name, id }))
  const allAdvancedOptions = program ? program.definitions.Collections : []

  allAdvancedOptions.sort((a, b) => {
    if (a.name.toLowerCase() < b.name.toLowerCase()) return -1
    if (a.name.toLowerCase() > b.name.toLowerCase()) return 1
    return 0
  })
  projectDict.sort((a, b) => {
    if (a.name.toLowerCase() < b.name.toLowerCase()) return -1
    if (a.name.toLowerCase() > b.name.toLowerCase()) return 1
    return 0
  })

  function submitSearch (params, advancedParams) {
    const newParams = { ...(params || searchParamsTmp) }
    const newAdvanced = [...(advancedParams || advancedVals)]

    if (newParams.projType === '__all') delete newParams.projType

    const advFinal = newAdvanced && newAdvanced.length > 0 && newAdvanced
      .filter(([k, v]) => k && v && k.length > 0 && v.length > 0)

    const submitParams = { ...newParams }
    if (advFinal && advFinal.length > 0) submitParams.metaArr = JSON.stringify(advFinal)

    if (advancedParams) setAdvancedVals(advancedParams)
    setSearchParamsTmp(newParams)
    setSearchParams(submitParams)
  }

  const handleKeyPress = e => {
    if (e.keyCode === 13) {
      setSearchParams(searchParamsTmp)
    }
  }
  return (
    <Paper className={classes.root}>
      {program && program.products && (
        <div className={classes.projTypeLine}>
          <FormControl fullWidth className={classes.projType}>
            <InputLabel htmlFor='projectType'>Project Type</InputLabel>
            <Select
              id='projectType'
              inputProps={{
                className: classes.projTypeMenuItemSelected
              }}
              value={searchParamsTmp.projType || '__all'}
              onChange={(event) => {
                const projType = event.target.value !== '_all' ? event.target.value : null
                submitSearch({ ...searchParamsTmp, projType })
              }}
            >
              <MenuItem className={classes.projTypeMenuItem} value='__all'>
                <div className={classes.projTypeMenuItemName}>All Project Types</div>
                <div className={classes.projTypeMenuItemNum} variant='caption' color='textSecondary'>{projTotals.__all > -1 ? `${projTotals.__all}` : '...'}</div>
              </MenuItem>
              {projectDict.map(({ name: prodName, id: prodId }, idx) => (
                <MenuItem className={classes.projTypeMenuItem} key={`prod-${idx}`} value={prodId}>
                  <div className={classes.projTypeMenuItemName}>{prodName}</div>
                  <div className={classes.projTypeMenuItemNum} variant='caption' color='textSecondary'>{projTotals[prodId] > -1 ? `${projTotals[prodId]}` : '...'}</div>
                </MenuItem>
              ))}
            </Select>
          </FormControl>
          <div className={classes.spacer} />
          <FormControl className={classes.resultsNum}>
            <InputLabel htmlFor='resultsNum'>Results</InputLabel>
            <Select
              id='resultsNum'
              value={searchParamsTmp.limit}
              onChange={(event) => {
                submitSearch({ ...searchParamsTmp, limit: event.target.value })
              }}
            >
              <MenuItem value={10}>10</MenuItem>
              <MenuItem value={20}>20</MenuItem>
              <MenuItem value={50}>50</MenuItem>
              <MenuItem value={100}>100</MenuItem>
            </Select>
          </FormControl>
        </div>
      )}

      <TextField
        id='outlined-helperText2'
        label='Metadata Search'
        fullWidth
        autoFocus
        placeholder='...'
        className={classes.textField}
        margin='normal'
        variant='outlined'
        onKeyDown={handleKeyPress}
        onChange={(event) => {
          setSearchParamsTmp({ ...searchParamsTmp, metaStr: event.target.value })
        }}
      />
      {advancedVals.length > 0 && (
        <div className={classes.advanced}>
          {advancedVals.map((row, rid) => (
            <AdvancedRow
              key={`advRow-${rid}`}
              classes={classes}
              row={row}
              options={allAdvancedOptions.filter(ao => {
                return !advancedVals.find((row, idx) => idx !== rid && row[0] === ao.name)
              })}
              changeHandler={(newRow, forceSubmit) => {
                const newAdvanced = [...advancedVals]
                newAdvanced[rid] = newRow
                setAdvancedVals(newAdvanced)
                if (forceSubmit) submitSearch(null, newAdvanced)
              }} />
          ))}
          <div>
            <Button onClick={() => submitSearch(null, [...advancedVals, ['', '']])}>+</Button>
            <Button onClick={() => submitSearch(null, advancedVals.slice(0, -1))}>-</Button>
          </div>
        </div>
      )}
      <div className={classes.actionBar}>
        <Button color='primary' onClick={toggleAdvanced}>{advancedVals.length > 0 ? 'Hide' : 'Show'} Advanced</Button>
        <div className={classes.spacer} />
        <Button
          className={classes.iconButton}
          color='primary'
          variant='contained'
          onClick={() => {
            submitSearch(searchParamsTmp)
          }}
        >
          <SearchIcon /> Search
        </Button>
        <Button
          className={classes.iconButton}
          color='secondary'
          variant='contained'
          onClick={() => {
            setAdvancedVals([])
            submitSearch(appDefaults.searchDefaultParams)
          }}
        >
              Reset
        </Button>

      </div>
    </Paper>

  )
}

export default SearchForm

SearchForm.propTypes = {
  projTotals: PropTypes.object
}

const useStylesAdv = makeStyles(theme => ({
  root: {
    display: 'flex'
  },
  advancedLeft: {
    flex: '1 1 30%',
    height: theme.spacing(4),
    marginRight: theme.spacing(1)
  },
  advancedLeftCustom: {
    textAlign: 'center'
  },
  advancedLeftOption: {
  },
  advancedText: {
    flex: '1 1 70%'
  },
  advancedRight: {
    flex: '1 1 70%'
  },
  advancedOp: {
    display: 'flex'
  },
  advancedTextCustomName: {
    flex: '1 1 30%',
    marginRight: theme.spacing(1)
  },
  advancedTextCustomValue: {
    flex: '1 1 40%'
  },
  advancedOpName: {
    flexGrow: 1
  },

  advancedOpNamId: {

  },
  advancedInput: {
    height: theme.spacing(4)
  }
}))

const AdvancedRow = ({ row, options, changeHandler }) => {
  const classes = useStylesAdv()
  const [mKey, mVal] = row
  const [err, setErr] = useState()

  const option = mKey && mKey.length > 0 && options.find(o => o.name.toLowerCase() === mKey.toLowerCase())
  const hasSetVals = option && option.Allow && !!option.Allow.find(o => o.attr.name.toLowerCase() && o.attr.name.length > 0)

  const validate = (key, val) => {
    // regex validation
    if (val && val.length > 0 && option && !hasSetVals) {
      const allowRegex = option.AllowRegex.filter(o => o.attr.pattern)
      if (allowRegex && allowRegex.length > 0) {
        const errs = []
        allowRegex.forEach(regex => {
          console.log(regex)
          const pattern = new RegExp(regex.attr.pattern)
          const hasError = !val.match(pattern)
          if (hasError) {
            errs.push(pattern)
          }
        })
        // Bail on the query if there are any errors. Display them in the error column
        if (errs.length > 0) {
          if (errs.length === 1) setErr(`Value must match pattern: '${errs[0]}'`)
          else {
            setErr(errs.reduce((acc, err, idx) => acc + idx > 0 ? ' and ' : '' + err, 'Value must match patterns: '))
          }
          return false
        }
      }
    }
    setErr(null)
    console.log('CHANGING VALUES', [key, val])
    if (key.toLowerCase() !== mKey.toLowerCase() || val.toLowerCase() !== mVal.toLowerCase()) {
      changeHandler([key, val])
    }
  }

  const debouncedValidate = util.debounce(validate, 500)

  const handleKeyPress = keyCode => {
    if (keyCode === 13 && mKey && mKey.length > 0) {
      console.log('Enter pressed')
      changeHandler([mKey, mVal], true)
    }
  }

  return (
    <div className={classes.root}>
      <FormControl className={classes.advancedLeft}>
        <Select
          value={option ? option.name : '__custom'}
          inputProps={{
            className: classes.advancedInput
          }}
          onChange={event => changeHandler([event.target.value, ''], true)}
        >
          <MenuItem key={`aoss-custom`} value='__custom'>
            <Typography className={classes.advancedLeftCustom} color='textSecondary'>
              <em>Custom</em>
            </Typography>
          </MenuItem>
          {options.map(({ name }, oid) => (
            <MenuItem key={`aoss-${oid}`} value={name}>
              <Typography className={classes.advancedLeftOption}>{name}</Typography>
            </MenuItem>
          ))}
        </Select>
      </FormControl>
      {hasSetVals ? (
        <FormControl className={classes.advancedRight}>
          <Select
            value={mVal}
            inputProps={{
              className: classes.advancedInput
            }}
            onChange={event => changeHandler([mKey, event.target.value], true)}
          >
            {option.Allow.map(({ attr }, oid) => {
              return (
                <MenuItem className={classes.advancedOp} key={`aooptss-${oid}`} value={attr.id}>
                  <Typography className={classes.advancedOpName}>{attr.name}</Typography>
                  {attr.name !== attr.id && (
                    <Typography variant='caption' className={classes.advancedOpNamId}>{attr.id}</Typography>
                  )}
                </MenuItem>
              )
            })}
          </Select>
        </FormControl>
      ) : option ? (
        <TextField
          error={!!err}
          label={err}
          disabled={!mKey || mKey.length === 0}
          placeholder='...'
          defaultValue={mVal}
          className={classes.advancedText}
          inputProps={{
            className: classes.advancedInput
          }}
          onKeyDown={e => handleKeyPress(e.keyCode)}
          onChange={e => debouncedValidate(mKey, e.target.value)}
        />
      ) : (
        <React.Fragment>
          <TextField
            placeholder='Meta Name'
            defaultValue=''
            className={classes.advancedTextCustomName}
            inputProps={{
              className: classes.advancedInput
            }}
            onKeyDown={e => handleKeyPress(e.keyCode)}
            onChange={e => debouncedValidate(e.target.value, mVal)}
          />
          <TextField
            placeholder='Meta Value'
            defaultValue=''
            className={classes.advancedTextCustomValue}
            inputProps={{
              className: classes.advancedInput
            }}
            onKeyDown={e => handleKeyPress(e.keyCode)}
            onChange={e => debouncedValidate(mKey, e.target.value)}
          />
        </React.Fragment>
      )}
    </div>
  )
}

AdvancedRow.propTypes = {
  row: PropTypes.array,
  options: PropTypes.array,
  changeHandler: PropTypes.func
}
