import red from '@material-ui/core/colors/red'
import green from '@material-ui/core/colors/green'
import blue from '@material-ui/core/colors/blue'
import orange from '@material-ui/core/colors/orange'
import purple from '@material-ui/core/colors/purple'
import cyan from '@material-ui/core/colors/cyan'
import lime from '@material-ui/core/colors/lime'
import brown from '@material-ui/core/colors/brown'

let baseurl = process.env.REACT_APP_BASEURL
let basename = process.env.REACT_APP_BASENAME
let domain = process.env.REACT_APP_DOMAIN
if (process.env.NODE_ENV && process.env.NODE_ENV === 'development') {
  domain = 'localhost'
  basename = ''
}
console.log('config: ', { baseurl, basename, domain })

export const version = process.env.REACT_APP_VERSION

export const endpoints = {
  graphql: `${process.env.REACT_APP_GRAPHQL}`
}

export const appDefaults = {
  searchDefaultParams: {
    limit: 10
  }
}

export const mandatoryMetaColors = [red, green, blue, orange, purple, cyan, lime, brown]

export const aws = {
  Auth: {
    // REQUIRED - Amazon Cognito Identity Pool ID
    // identityPoolId: process.env.REACT_APP_COGNITO_IDPOOLID, // XX-XXXX-X:XXXXXXXX-XXXX-1234-abcd-1234567890ab
    // REQUIRED - Amazon Cognito Region
    region: process.env.REACT_APP_AWS_REGION_DEPLOY,
    // OPTIONAL - Amazon Cognito User Pool ID
    userPoolId: process.env.REACT_APP_AWS_USERPOOLID,
    // OPTIONAL - Amazon Cognito Web Client ID (26-char alphanumeric string)
    userPoolWebClientId: process.env.REACT_APP_AWS_USERPOOLWEBCLIENTID,
    // OPTIONAL - Enforce user authentication prior to accessing AWS resources or not
    mandatorySignIn: true,
    // OPTIONAL - Configuration for cookie storage
    cookieStorage: {
      // REQUIRED - Cookie domain (only required if cookieStorage is provided)
      domain: `${domain}`,
      // OPTIONAL - Cookie path
      // path: `/${basename}`,
      // OPTIONAL - Cookie expiration in days
      expires: 120,
      // OPTIONAL - Cookie secure flag
      secure: false
    }

  }

}
