import React, { useState, useEffect, useRef, useContext } from 'react'
import SearchForm from './SearchForm'
// import PropTypes from 'prop-types'
import { AppContext } from '../../contexts'
import { api } from '@riverscapes/common'

const SearchFormConnect = () => {
  const [projTotals, setProjTotals] = useState({})
  const projTotalsRef = useRef()
  projTotalsRef.current = projTotals
  const { jwt, url, program } = useContext(AppContext)

  useEffect(() => {
    if (program && jwt && url) {
      // The initial search is just to get at the total value
      api.search(url, jwt, program.machineName, { limit: 0 })
        .then(({ data }) => setProjTotals({ ...projTotalsRef.current, __all: data.search ? data.search.total : 0 }))
        .catch((err) => {
          console.error(err)
        })
      // HEre's where we get the product totals
      Object.keys(program.products)
        .reduce((promiseChain, projId) => {
          const currTask = () => api.search(url, jwt, program.machineName, { projType: projId, limit: 0 })
            .then(({ data }) => {
              setProjTotals({ ...projTotalsRef.current, [projId]: data.search ? data.search.total : 0 })
            })
            .catch((err) => {
              setProjTotals({ ...projTotalsRef.current, [projId]: 0 })
              console.error(err)
            })
          return promiseChain.then(currTask)
        }, Promise.resolve())
    }
  }, [program, jwt, url])

  return <SearchForm projTotals={projTotals} />
}

export default SearchFormConnect
