import React, { useContext } from 'react'
import PropTypes from 'prop-types'
import { makeStyles } from '@material-ui/core/styles'
import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
import Chip from '@material-ui/core/Chip'
import IconButton from '@material-ui/core/IconButton'
import Divider from '@material-ui/core/Divider'
import CloseIcon from '@material-ui/icons/Close'
import Typography from '@material-ui/core/Typography'
import InsertDriveFileIcon from '@material-ui/icons/InsertDriveFile'
import PhotoIcon from '@material-ui/icons/Photo'
import Terrain from '@material-ui/icons/Terrain'
import Assignment from '@material-ui/icons/Assignment'

import TreeView from '@material-ui/lab/TreeView'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore'
import ChevronRightIcon from '@material-ui/icons/ChevronRight'

import TreeItem from '@material-ui/lab/TreeItem'

import filesize from 'filesize'
import moment from 'moment'
import { red, purple, green } from '@material-ui/core/colors'

import { projectHelpers } from '@riverscapes/common'

import { AppContext } from '../contexts'

const useStyles = makeStyles(theme => ({
  root: {
    margin: 0,
    padding: theme.spacing(2)
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500]
  },
  normalIcon: {

  },
  documentIcon: {
    color: red[500]
  },
  photoIcon: {
    color: purple[500]
  },
  TerrainIcon: {
    color: green[500]
  },
  metaDisplay: {
    display: 'flex',
    flexFlow: 'wrap'
  },
  title: {
    marginTop: theme.spacing(2)
  },
  treeLabel: {
    marginLeft: theme.spacing(1),
    '&>*': {
      lineHeight: 1.2
    }
  },
  treeFile: {
  },
  treeFileContent: {
    // border: '1px solid blue'
    borderBottom: `1px solid ${theme.palette.grey[100]}`,
    padding: theme.spacing(1)
  },
  treeFolder: {

  },
  treeFolderContent: {
    // border: '1px solid green'
    padding: theme.spacing(1)
  },
  metaDisplayItem: {
    flex: '1 1',
    maxWidth: '25%',
    minWidth: 150,
    margin: theme.spacing(0, 1, 0, 0),
    '&>*': {
      whiteSpace: 'nowrap',
      overflow: 'hidden',
      textOverflow: 'ellipsis'
    }
  },
  prodChip: {
    // height: theme.spacing(2.3),
    borderRadius: theme.spacing(1) / 2,
    color: '#999',
    border: `1px solid #ddd`,
    background: '#eee',
    marginRight: theme.spacing(1)
  }
}))

export default function CustomizedDialogs ({ onClose, project }) {
  const classes = useStyles()
  const { downloadFile, program } = useContext(AppContext)

  const iconMapping = [
    { pattern: /.*\.(png|gif|jpg|jpeg)$/i, icon: <PhotoIcon className={classes.photoIcon} /> },
    { pattern: /.*\.(tif|img|shp)$/i, icon: <Terrain className={classes.TerrainIcon} /> },
    { pattern: /.*\.(csv|pdf|doc)$/i, icon: <Assignment className={classes.documentIcon} /> }
  ]

  const files = project.files.reduce((acc, file) => {
    const found = iconMapping.find(({ pattern }) => file.key.match(pattern))
    return {
      ...acc,
      [file.key]: {
        ...file,
        icon: found ? found.icon : <InsertDriveFileIcon className={classes.normalIcon} />,
        classStr: found ? found.classStr : 'normalIcon'
      }
    }
  }, {})
  const fileTree = projectHelpers.treeIzeFilePaths(Object.keys(files))
  let pTypeName = program.products[project.projType].name
  return (
    <Dialog
      onClose={onClose}
      open
      maxWidth='md'
      fullWidth
    >
      <DialogTitle disableTypography className={classes.root}>
        <div>
          <Typography variant='h5'>{project.name}</Typography>
          <Typography variant='overline' color='textSecondary'>
            <Chip className={classes.prodChip} label={pTypeName} />
            ID: {project.id}
          </Typography>
        </div>
        {onClose ? (
          <IconButton aria-label='close' className={classes.closeButton} onClick={onClose}>
            <CloseIcon />
          </IconButton>
        ) : null}
      </DialogTitle>
      <DialogContent dividers>
        <Typography variant='caption' component='div' color='inherit'>
          <strong>Created</strong>: {moment(project.createdOn, 'x').fromNow()} ({moment(project.createdOn, 'x').format('lll')})
        </Typography>
        <Typography variant='caption' component='div' color='inherit'>
          <strong>Updated</strong>: {moment(project.updatedOn, 'x').fromNow()} ({moment(project.updatedOn, 'x').format('lll')})
        </Typography>

        <Typography className={classes.title} color='inherit' variant='subtitle1'>Metadata</Typography>
        <Divider />
        <div className={classes.metaDisplay}>
          {Object.keys(project.meta).map((metaKey, idk) => (
            <div key={`result-${idk}`} className={classes.metaDisplayItem}>
              <Typography variant='overline' component='div' color='textSecondary'>{metaKey}</Typography>
              <Typography variant='caption' component='div' color='textPrimary'>{project.meta[metaKey]}</Typography>
            </div>
          ))}
        </div>

        <Typography className={classes.title} color='inherit' variant='subtitle1'>Files</Typography>
        <Divider />
        <TreeView
          className={classes.treeRoot}
          defaultExpanded={['1']}
          defaultCollapseIcon={<ExpandMoreIcon color='inherit' />}
          defaultExpandIcon={<ChevronRightIcon color='inherit' />}
        >
          {
            fileTree.map((ft, idx) => <RecurseTreeItem project={project} key={`treeNode-${idx}`} keyStr={`treeNode-${idx}`} classes={classes} item={ft} fileDict={files} fileClick={downloadFile} />)
          }
        </TreeView>
      </DialogContent>
    </Dialog>
  )
}

CustomizedDialogs.propTypes = {
  onClose: PropTypes.func,
  project: PropTypes.object
}

const RecurseTreeItem = ({ classes, item, fileDict, keyStr, project, fileClick }) => {
  if (item.children) {
    const childNodes = item.children.map((c, idx) => (
      <RecurseTreeItem classes={classes} keyStr={`${keyStr}-l${idx}`} key={`${keyStr}-l${idx}`} item={c} fileDict={fileDict} project={project} fileClick={fileClick} />
    ))
    return (
      <TreeItem
        nodeId={keyStr}
        className={classes.treeFolder}
        classes={{
          content: classes.treeFolderContent
        }}
        label={item.name}
        children={childNodes}
      />
    )
  }
  else {
    const lookup = item.path ? fileDict[item.path] : null
    return (
      <TreeItem
        nodeId={keyStr}
        onClick={() => fileClick(project, item.path)}
        className={classes.treeFile}
        classes={{
          content: classes.treeFileContent
        }}
        label={(
          <div className={classes.treeLabel}>
            <Typography variant='caption' component='div' color='textPrimary'>{item.path}</Typography>
            <Typography variant='caption' component='div' color='textSecondary'>{filesize(parseInt(lookup.size, 10))}</Typography>
          </div>
        )}
        endIcon={lookup.icon}
      />

    )
  }
}

RecurseTreeItem.propTypes = {
  item: PropTypes.object,
  classes: PropTypes.object,
  fileDict: PropTypes.object,
  project: PropTypes.object,
  fileClick: PropTypes.func,
  keyStr: PropTypes.string
}
