import React, { useState, useContext } from 'react'
import PropTypes from 'prop-types'
import Paper from '@material-ui/core/Paper'
import Divider from '@material-ui/core/Divider'
import { AppContext } from '../../contexts'

import List from '@material-ui/core/List'
import SearchResult from './SearchResult'

import ProjectDialog from '../ProjectDialog'
import { makeStyles } from '@material-ui/core/styles'

import Pagination from '../Pagination'

const useStyles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(2),
    margin: theme.spacing(2)
  }
}))

const SearchResults = ({ data, loading, errors }) => {
  const classes = useStyles()
  const [projDialog, setProjDialog] = useState()
  const { searchParams } = useContext(AppContext)

  if (!loading && (
    !data || !data.projects || data.projects.length === 0
  )) {
    return (
      <Paper className={classes.root}>
        <div>No Projects Found</div>
      </Paper>
    )
  }

  return (
    <Paper className={classes.root}>
      <Pagination data={data} loading={loading} />
      <Divider />
      <List>
        {loading && (
          Array(searchParams.limit || 10).fill(null).map((x, idx) => (
            <SearchResult key={`result-${idx}`} loading />
          ))
        )}
        {!loading && data && data.projects.map((r, idr) => (
          <SearchResult key={`result-${idr}`} data={r} onClick={() => setProjDialog(r)} />
        ))}
      </List>
      {projDialog && <ProjectDialog project={projDialog} onClose={() => setProjDialog()} />}
    </Paper>
  )
}

SearchResults.propTypes = {
  data: PropTypes.object,
  loading: PropTypes.bool,
  errors: PropTypes.array
}

export default SearchResults
