import React, { useContext, useState } from 'react'
import classNames from 'classnames'
import Toolbar from '@material-ui/core/Toolbar'
import AppBar from '@material-ui/core/AppBar'
import Divider from '@material-ui/core/Divider'
import IconButton from '@material-ui/core/IconButton'
import MenuItem from '@material-ui/core/MenuItem'
import Menu from '@material-ui/core/Menu'
import MoreIcon from '@material-ui/icons/MoreVert'
import { AppContext } from '../contexts'

// import MenuIcon from '@material-ui/core/Menu'
import { fade, makeStyles } from '@material-ui/core/styles'
import { Typography } from '@material-ui/core'

const style = theme => ({
  root: {
    display: 'flex',
    alignItems: 'center',
    width: '100%',
    padding: theme.spacing(0, 1),
    textShadow: '3px 3px 3px rgba(0,0,0,0.7)',
    backgroundImage: `url(${process.env.PUBLIC_URL}/HeaderBackground.png)`,
    backgroundRepeat: 'no-repeat',
    backgroundPosition: '0% 50%',
    backgroundSize: 'cover',
    backgroundColor: '#444444'
  },
  logo: {
    height: 40,
    width: 40,
    marginRight: theme.spacing(2)
  },
  programChooser: {
    margin: theme.spacing(0, 1),
    color: theme.palette.primary.contrastText,
    '& svg': {
      color: theme.palette.primary.contrastText
    },
    '&:before': {
      borderColor: theme.palette.primary.contrastText
    },
    '&:after': {
      borderColor: theme.palette.primary.contrastText
    }
  },
  transparent: {
    backgroundColor: 'transparent',
    background: 'none',
    color: theme.palette.text.primary
    // borderBottom: `2px transparent`
  },
  search: {
    position: 'relative',
    borderRadius: theme.shape.borderRadius,
    backgroundColor: fade(theme.palette.common.white, 0.15),
    '&:hover': {
      backgroundColor: fade(theme.palette.common.white, 0.25)
    },
    marginRight: theme.spacing(2),
    marginLeft: 0,
    width: '100%',
    flexGrow: 1,
    [theme.breakpoints.up('sm')]: {
      marginLeft: theme.spacing(3),
      width: 'auto'
    }
  },
  searchIcon: {
    width: theme.spacing(7),
    height: '100%',
    position: 'absolute',
    pointerEvents: 'none',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center'
  },
  inputRoot: {
    color: 'inherit'
  },
  spacer: {
    flexGrow: 1
  },
  inputInput: {
    padding: theme.spacing(1, 1, 1, 7),
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      width: 200
    }
  },
  sectionDesktop: {
    display: 'none',
    [theme.breakpoints.up('sm')]: {
      display: 'flex',
      flexGrow: 1
    }
  },
  sectionMobile: {
    display: 'flex',
    flexGrow: 1,
    [theme.breakpoints.up('sm')]: {
      display: 'none'
    }
  }
})
const useStyles = makeStyles(style)

const TopBar = () => {
  const classes = useStyles()
  const { program, programs, signOut, navigateProgram } = useContext(AppContext)

  const [mobileMoreAnchorEl, setMobileMoreAnchorEl] = useState(null)

  const handleMobileMenuClose = () => {
    setMobileMoreAnchorEl(null)
  }

  const handleNavigate = (nm) => {
    handleMobileMenuClose()
    navigateProgram(nm)
  }

  const handleMobileMenuOpen = event => {
    setMobileMoreAnchorEl(event.currentTarget)
  }

  const renderMobileMenu = programs && (
    <Menu
      anchorEl={mobileMoreAnchorEl}
      anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
      keepMounted
      transformOrigin={{ vertical: 'top', horizontal: 'right' }}
      open={!!mobileMoreAnchorEl}
      onClose={handleMobileMenuClose}
    >
      <MenuItem>
        <Typography variant='overline'>Programs</Typography>
      </MenuItem>
      {programs.map((prog, idx) => (
        <MenuItem
          disabled={program && program.machineName === prog.machineName}
          key={`prog-${idx}`}
          onClick={() => handleNavigate(prog.machineName)}
          value={prog.machineName}
        >
          {prog.name}
        </MenuItem>
      ))}
      <Divider />
      <MenuItem onClick={signOut}>
      Log Out
      </MenuItem>
    </Menu>
  )

  return (
    <React.Fragment>
      <AppBar position='static'>
        <Toolbar className={classNames(classes.root)} disableGutters>
          <a href='http://riverscapes.xyz/'>
            <img className={classes.logo} src={process.env.PUBLIC_URL + '/RiverscapesLogo.png'} alt='logo' />
          </a>
          {program
            ? <div>
              <Typography variant='subtitle1'>{program.name}</Typography>
              <Typography variant='caption'>Riverscapes Warehouse Explorer</Typography>
            </div>
            : <Typography variant='subtitle1'>Riverscapes Warehouse Explorer</Typography>
          }
          <div className={classes.spacer} />
          <IconButton
            aria-haspopup='true'
            onClick={handleMobileMenuOpen}
            color='inherit'
          >
            <MoreIcon />
          </IconButton>

        </Toolbar>
      </AppBar>
      {programs && renderMobileMenu}
    </React.Fragment>
  )
}

export default TopBar
